import React from "react";

const Chinese =(props) => {

    var styleCont = {
        display:"flex",
        flexDirection: "row",
        textAlign: "center",
        flexWrap: "wrap",
        justifyContent:"center",
        alignItems: "center"  ,
        marginTop:"12vh"
    }
    var styleSymbol = {
        fontSize:"4vw",
        color:"deeppink"
    }    
    var styleSymTrans = {
        border:"2px solid navy",
        margin:"3px",
        padding:"5px"
    }    
    var symbolsArray =[{sym:"", trans:"China", read:"Zhnggu"}, {sym:"", trans:"One", read:"Y"}, {sym:"", trans:"Two", read:"r"},{sym:"", trans:"Three", read:"Sn"},
                        {sym:"", trans:"Four", read:"S"}, {sym:"", trans:"Five", read:"W"},{sym:"", trans:"Six", read:"Li"}, {sym:"", trans:"Seven", read:"Q"},
                        {sym:"", trans:"Eight", read:"B"}, {sym:"", trans:"Nine", read:"Ji"}, {sym:"", trans:"Ten", read:"Sh"},
                        {sym:"", trans:"person", read:"Rn"}, {sym:"",trans:"husband",read:"F"}, {sym:"",trans:"entrance",read:"Rku"},
                        {sym:"",trans:"exit",read:"Chku"}, {sym:"",trans:"out",read:"Ch"}, {sym:"", trans:"fire", read:"Hu" }, {sym:"", trans:"toilet", read:"Csu"},
                        {sym:"", trans:"food", read:"Cnyn"}, {sym:"", trans:"police", read:"Jngch"}, {sym:"", trans:"please", read:"Qng"},
                        {sym:"", trans:"you", read:"N"}, {sym:"", trans:"name", read:"Mng"}, {sym:"", trans:"yes", read:"Sh"}, {sym:"", trans:"big/great", read:"D"}
    ];
    
    return (
        <React.Fragment>
            <div style={styleCont} className="TopMarginToDealWithNavBar">   
               
                {symbolsArray.map( (item, index) =>   
                    <div style={styleSymTrans} key={index}>                           
                        <span  style={styleSymbol}>{item.sym}</span><br/>         
                        <span style={{fontWeight:"bold"}}>{item.trans}</span>  <br/>  
                        <hr/>
                        <span>{item.read}</span>        
                    </div>                  
                )}
            </div>
        </React.Fragment>
        )
}
export default Chinese;
